.spinner {
  margin: 7px;
  display: flex;
  .circle {
    width: 36px;
    height: 36px;
    border-width: 5px;
    border-style: solid;
    border-radius: 50%;
    border-color: #2c3e50;
    border-top-color: transparent !important;
    animation: rotate 1s linear infinite;
  }
}
@keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}